 
.brand-title {
  font-family: 'Kaushan Script', cursive;
  font-size: large;
  font-weight: bolder; 
  }
   
  /* square buttons */
  .rec.rec-arrow {
    border-radius: 2px ;
    border: #00AF87;
    background: #eafaf4;
}
/* round buttons on hover */
.rec.rec-arrow:hover {
    border-radius: 0;
    background: #00AF87 !important;
}

.rec.rec-arrow:focus {
    background: #00AF87 !important;
}
/* hide disabled buttons */
.rec.rec-arrow:disabled {
    visibility: hidden;
}
/* disable default outline on focused items */
/* add custom outline on focused items */
.rec-carousel-item:focus {
    outline: none;
    box-shadow: inset 0 0 1px 1px #00AF87;
}

.rec.rec-dot {
    visibility: hidden;
  }

 
   